.App {
   
  font-family: 'PT Sans', sans-serif;
  /* padding: 5px 20px; */
  overflow: hidden;
  color:var(--black)
}

:root{
 --yellow:#f5c71a;
 --black:#263159;
 --gray:#65647C;
 --purple:#F3CCFF;
 --orange:#FFD700;
 --lightgreen:#9ED5C5;
 --boxShadow:0px 23px 30px rgb(0 0 0 / 13%);
 --smboxshadow:-69px 41px 50px rgba(0,0,0,0.05);
}



.button{
  border-radius:34px;
  border:none;
  color:white;
  font-size: 11px;
  font-weight: 800;
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
  padding:11px 26px 11px 26px;
  background: linear-gradient(100deg, #fdc50f 26.71%, #fb982f 99.36%);
}
.button:hover {
  background: white;
  cursor: pointer;
  border: 1px solid var(--orange);
  
  color: var(--orange);
}

























