.profilemain{
    /* background-image: url("https://i.ibb.co/cLZRyLK/pngwing-com-3.png" ); */
     background-repeat: no-repeat;
     background-size: cover;
     /* transition: .5s; */
    height: 500px;
    
    display: flex;
    justify-content: space-around;
    /* border: 1px solid red; */
    width: 85%;
    margin: auto;
    margin-top: 85px;
}
.hey{
    width:70px;
    
}
.myself{
    font-size: 33px;
    margin-left: 10%;
    transition: .5s;
    letter-spacing: 5px;
    transition: .5s;
    font-family: 'Signika Negative', sans-serif;
}
.dynamic-txts{
    font-size: 53px;
    color: var(--yellow);
    font-family: 'Signika Negative', sans-serif;

}
.skilled{
   font-size: 28px; 
   margin-left: 10%;
   transition: .5s;
   letter-spacing: 3px;
   margin-top:30%;
}

.buttresu{
    margin-left: 10%;
    
    height: 9vh;
    font-size: 22px;
    transition: .8s;
    padding: 20px 50px;
    text-decoration: none;
}
.opac{
    opacity: 1;
    color: black;
}
.mainImage{
    position: absolute;
    left: 65%;
    border-radius: 30%;
    top:20%;
    width:17%;
    transition: .5s;
    box-shadow: rgba(20, 35, 248, 0.25) 0px 54px 55px, rgba(196, 19, 19, 0.12) 0px -12px 30px, rgba(11, 238, 60, 0.12) 0px 4px 6px, rgba(244, 236, 14, 0.17) 0px 12px 13px, rgba(4, 237, 245, 0.09) 0px -3px 5px;
}





@media screen and (min-width: 750px) and (max-width: 1010px){
.myself{
    font-size: 40px;
}
.mainImage{
    width:26%; 
}
.skilled{
    font-size: 20px; 
    margin-left: 10%;
 }
.mainbackImage{
    width:100%
}
.dynamic-txts{
    font-size: 40px;
    color: var(--yellow);
    font-family: 'Signika Negative', sans-serif;

}
}

@media screen and (min-width: 490px) and (max-width: 750px){
    .profilemain{
        justify-content: space-around;
        height:650px
    }
    .myself{
        font-size: 28px;
    }
    .dynamic-txts{
        font-size: 33px;
        color: var(--yellow);
        font-family: 'Signika Negative', sans-serif;
    }
    .mainImage{
        width:32%; 
        left: 55%;
        border-radius: 30%;
        top:38%;
    }
    .skilled{
        font-size: 20px; 
        margin-top: 280px;
        margin-left: 10%;
     }
    .mainbackImage{
        position: absolute;
        top: 220px;
        opacity: 0.3;
        left: 30%;
        width:60%
    }
    .buttresu{
        margin-left: 10%;
        height: 5vh;
        font-size: 15px;
        padding: 20px 20px;
        text-decoration: none;
    }
}
@media screen and (max-width: 490px){
    .profilemain{
        justify-content: space-around;
        height:520px;
    }
    .profilemain{
     flex-direction:column;
    }
    .myself{
        font-size: 25px;
        margin-top: 10px;
    }
    .mainImage{
        width:160px; 
        top: 240px;
        left: 30%;
    }
    .skilled{
        font-size: 20px; 
        margin-top: 290px;
     }
    .mainbackImage{
        position: absolute;
        top: 260px;
        opacity: 0;
        left: 10%;
        width:80%
    }
    .buttresu{
        font-size: 20px;
        padding: 20px 20px;
        text-decoration: none;
        margin-top: 0;
    }
    .dynamic-txts{
        font-size: 34px;
        color: var(--yellow);
    }
    .hey{
        width:50px;
        
    }
}

